import * as React from "react"
import { Link } from "gatsby"
import { Navbar, Container } from "react-bootstrap";
import Image from "./image";

import headerLogo from "../images/header-logo.png"
import headerLogoSm from "../images/header-logo-sm.png"
import headerLogoMd from "../images/header-logo-md.png"
import headerLogo2x from "../images/header-logo@2x.png"
import headerLogoSm2x from "../images/header-logo-sm@2x.png"
import headerLogoMd2x from "../images/header-logo-md@2x.png"

function Header ({ siteTitle, props }) {

    console.log("Tengo props:_ ", props);
    let pathName = props?.location.pathname;
    var logoPrimos = "";

    if(pathName === "/chevrolet/") {
        logoPrimos = "somos_primos_chevrolet";
    } else {
        logoPrimos = "somos_primos_nissan";
    }

    return(
        <React.Fragment>
            <Navbar expand="lg" id="barraMenu">
                <Container className="px-5" fluid style={{ maxWidth: "1300px", display: 'flex', justifyContent: 'space-between' }}>
                        <div className="imagen-logo-rivero">
                            <Image
                                className="d-md-block d-none"
                                fileName={"logo_blanco_sin_primo"}
                                alt="banner"
                                imgStyles={{width: "100%" }}
                                style={{width: '100%' }}
                            />    
                        </div> 
                        <div className="imagen-logo-primos">
                            <Image
                                className="d-md-block d-none"
                                fileName={logoPrimos}
                                alt="banner"
                                imgStyles={{width: "100%" }}
                                style={{width: '100%' }}
                            />    
                        </div> 
                </Container>
            </Navbar>
        </React.Fragment>
  )
}

export default Header
